/* eslint-disable jsx-a11y/anchor-has-content */
import React from "react";
import { Link } from "react-router-dom";

export default function MNav() {
  return (
    <>
      <nav className="navbar navbar-expand-lg bg ">
        <div className="container-fluid ">
          <h1 className="fw-bolder mt-2 ms-2">2LKA7YAN</h1>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 me-3 align-items-center">
              <li className="nav-item ms-3 mt-2 mb-1">
                <Link className="fw-normal fs-5 nav-link text-info" to="/login">
                  Login
                </Link>
              </li>
              <li className="nav-item ms-3 mt-2 mb-1">
                <Link
                  className="fw-normal fs-5 nav-link text-info"
                  to="/register"
                >
                  Register
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}

