import React from 'react'
import { Navigate, RouterProvider, createHashRouter } from 'react-router-dom'
import Home from './components/Home/Home.jsx'
import Register from './components/Register/Register.jsx'
import Main from './components/main/main.jsx';
import Login from './components/Login/Login.jsx';
import Error from './components/errorpage/Error.jsx';
import Movie from './components/Movies/Movie.jsx';
import Series from './components/Sereis/Series.jsx';
import Mdetails from './components/details/Mdetails.jsx';
import SearchResults from './components/search/SearchResults.jsx';




function ProtectedRoute({children}) {

  

  if (localStorage.getItem("logged")===null){
    
      return <>
      <Navigate to="/" />
      </>
     
  
  }else{
    return <>
    {children}
    </>
  }


}




const router = createHashRouter([
  { path: "", element: <Login /> },
  { path: "/login", element: <Login /> },
  { path: "/register", element: <Register /> },
  { path: "/logOut", element: <Login /> },
  
  {
    path: "/home",
    element: <ProtectedRoute><Main/></ProtectedRoute>  ,
    children: [
      { path: "Home", element:  <ProtectedRoute> <Home /> </ProtectedRoute> },
      { path: "movie", element:   <ProtectedRoute> <Movie /> </ProtectedRoute> },
      { path: "series", element: <ProtectedRoute> <Series /></ProtectedRoute> },
      { path: "search", element: <ProtectedRoute><SearchResults /></ProtectedRoute> },
      { path: "Mdetails", element: <ProtectedRoute> <Mdetails/> </ProtectedRoute>, children:[
        { path: ":media", element: <ProtectedRoute> <Mdetails/> </ProtectedRoute> ,children:[
          { path: ":id", element: <ProtectedRoute> <Mdetails/> </ProtectedRoute>}
        ] },
      ]},
    ],
  },
  { path: "/*", element: <Error /> },
]);

export default function App() {
  return (
    <>
    <RouterProvider router={router}/>
    </>
  )
}
