import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { SearchContext } from "../../SearchContext.jsx";

export default function SearchResults() {
  const { searchTerm } = React.useContext(SearchContext);

  const [results, setResults] = useState(null);

   const vote = (vote) => {
     const roundedNumber = vote.toFixed(1);
     return roundedNumber;
   };

  const truncateTitle = (title) => {
    if (title.length > 15) {
      return title.slice(0, 15) + "...";
    }
    return title;
  };

  useEffect(() => {
    if (searchTerm) {
      axios
        .get(
          `https://api.themoviedb.org/3/search/multi?query=${searchTerm}&include_adult=false&language=en-US&page=1&api_key=f3ece01977792f6f8ee4bfa412d0f7de`
        )
        .then(({ data }) => {
          setResults(data.results);
        });
    }
  }, [searchTerm]);

  return (
    <>
      {results === null ? (
        <div className="vh-100 d-flex justify-content-center align-items-center">
          <i class="fa-solid fa-spinner fa-spin fa-7x"></i>
        </div>
      ) : (
        <div className="container mt-5">
          <div className="row ">
            {results
              .filter((movie) => movie.poster_path != null)
              .map((movie) => (
                <div
                  key={movie.id}
                  className="col-lg-2 col-md-4 col-sm-6 col-10 m-auto"
                >
                  <Link
                    className="text-decoration-none text-light"
                    to={movie.title ? `/home/Mdetails/movie/${movie.id}` : `/home/Mdetails/tv/${movie.id}`}
                  >
                    <div className="movie text-center">
                      <div className="ImgC position-relative">
                        <img
                          src={
                            `https://image.tmdb.org/t/p/w500/` +
                            movie.poster_path
                          }
                          className="w-100 rounded-1 cursor"
                          alt="movie"
                        />
                        <div className="nnnn ">
                          <i className="  absoluteIcon  fa-solid fa-star text-warning ">
                            <p className=" absoluteRate fw-bolder text-white">
                              {vote(movie.vote_average)}
                            </p>
                          </i>
                        </div>
                      </div>
                      <h6 className="Montserrat p-3 mb-sm ps-0">
                        {/* {truncateTitle(movie.title)} */}
                        {movie.title? truncateTitle(movie.title): truncateTitle(movie.name)}
                        
                    </h6>
                    </div>
                  </Link>
                </div>
              ))}
          </div>
        </div>
      )}
    </>
  );
}
