import Joi from "joi";
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import MNav from "../Nav/mini.jsx";

export default function Login() {
  const go = useNavigate();

  const [errors, setErrors] = useState({});
  const [user, setUser] = useState({
    email: "",
    password: "",
  });

  const userData = (e) => {
    const newUser = { ...user };
    newUser[e.target.id] = e.target.value;
    setUser(newUser);

    


    // Validate the input field after its value is changed
    const schema = getSchema(e.target.id);
    const { error } = schema.validate(newUser[e.target.id]);

    if (error) {
      setErrors({ ...errors, [e.target.id]: error.details[0].message });
    } else {
      // Remove the error message if the input field is valid
      setErrors({ ...errors, [e.target.id]: "" });
      setErrors({});

    }
  };

  // Get the validation schema for a specific input field
  function getSchema(field) {
    switch (field) {
      case "email":
        return Joi.string()
          .email({ minDomainSegments: 2, tlds: { allow: ["com", "net"] } })
          .message("Email doesn't exist")
          .required();
      case "password":
        return Joi.string()
          .pattern(/^[a-zA-Z0-9]{6,30}$/i)
          .message("Wrong Password")
          .required();
      default:
        return Joi.any();
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    // Retrieve the existing user registrations from local storage
    const existingRegistrations =
      JSON.parse(localStorage.getItem("userRegistrations")) || [];

    // Check if the provided email and password exist in the registrations
    const userExists = existingRegistrations.some(
      (existingUser) =>
        existingUser.email === user.email &&
        existingUser.password === user.password
    );

    if (userExists) {
      localStorage.setItem("logged", JSON.stringify(user));
      go("/home/Home");
      
    } else {
      // Display an error message for email and/or password
      const errorMessages = {};
      errorMessages.general = "Invalid email or password";
      if (
        !existingRegistrations.some(
          (existingUser) => existingUser.email === user.email
        )
      ) {
        errorMessages.email = "Email doesn't exist";
      }
      if (
        !existingRegistrations.some(
          (existingUser) => existingUser.password === user.password
        )
      ) {
        errorMessages.password = "Wrong Password";
      }

      setErrors(errorMessages);
    }
  };

  return (
    <>
      <MNav />
      <div className="w-75 m-auto mt-5  pt-5">
        <div className="row  mt-5">
          <div className="col-md-9 m-auto">
            <h2 className="Montserrat fw-normal my-3 text-info fw-semibold">
              Login Now
            </h2>
            <form onSubmit={handleSubmit}>
              <label className="Montserrat my-2" htmlFor="email">
                Email
              </label>
              <input
                onChange={userData}
                value={user.email}
                className="form-control Montserrat"
                type="email"
                id="email"
              />
              <p className="text-danger my-2">{errors.email}</p>

              <label className="Montserrat my-2" htmlFor="password">
                Password
              </label>
              <input
                onChange={userData}
                value={user.password}
                className="form-control Montserrat em"
                type="password"
                id="password"
              />
              <p className="text-danger my-2">{errors.password}</p>
              {/* General error message */}
              {errors.general && (
                <p className="text-danger my-2">{errors.general}</p>
              )}
              <div className="lo mt-4 d-flex justify-content-between align-items-center flex-wrap">
                <h5 className="Montserrat my-2">
                  Have an Account?{" "}
                  <Link
                    to="/register"
                    className="fw-semibold fs-5 text-decoration-none text-info"
                  >
                    Register Now
                  </Link>
                </h5>

                <button
                  type="submit"
                  className="btn btn-info my-2 ms-1 Montserrat me-3"
                >
                  Login
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
