import axios from "axios";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";

export default function Mdetails() {
  const [mDetails, setMDetails] = useState(null);
  let {media,id} = useParams();


  async function getMDetails() {
    const { data } = await axios.get(
      `https://api.themoviedb.org/3/${media}/${id}?language=en-US&api_key=f3ece01977792f6f8ee4bfa412d0f7de`
    );

    setMDetails(data);
  }

  useEffect(() => {
    getMDetails();
  });

  return (
    <>
      {mDetails === null ? (
        <div className="vh-100 d-flex justify-content-center align-items-center">
          <i className="fa-solid fa-spinner fa-spin fa-7x"></i>
        </div>
      ) : (
        <div className="container mt-5">
          <div className="row ">
            <div className="col-xl-3 col-lg-4 col-md-5 col-sm-8 col-10 m-auto ">
              <div className="poster ">
                <img
                  src={
                    `https://image.tmdb.org/t/p/w500/` + mDetails.poster_path
                  }
                  className="w-100"
                  alt="Movie Poster"
                />
              </div>
            </div>
            <div className="col-xl-9 col-lg-8 col-md-7 center">
              <div className="details">
                <h2 className="my-3 Montserrat text-info ">{mDetails.title ? mDetails.title : mDetails.name}</h2>
                <h6 className="littleD my-3 Montserrat des">
                  {mDetails?.tagline}
                </h6>
                {mDetails.genres.map((gen, index) => (
                  <span
                    key={index}
                    className="me-2 my-3  btn btn-info color Montserrat fw-medium"
                  >
                    {gen.name}
                  </span>
                ))}

                <h6 className="mb-4 Montserrat">
                  Vote:{" "}
                  <span className="text-info">{mDetails.vote_average}</span>
                </h6>
                <h6 className="mb-4 Montserrat">
                  Vote count:{" "}
                  <span className="text-info">{mDetails.vote_count}</span>
                </h6>
                <h6 className="mb-4 Montserrat">
                  {" "}
                  Popularity:{" "}
                  <span className="text-info">{mDetails.popularity}</span>
                </h6>
                <h6 className="mb-5 Montserrat">
                  Release date:{" "}
                  <span className="text-info">{mDetails.release_date ? mDetails.release_date:mDetails.first_air_date}</span>
                </h6>
                <h6 className="Montserrat des mb-5">{mDetails.overview}</h6>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
