import axios from "axios";
import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";



export default function Home() {
  
  
  const [movies, setmovies] = useState(null);
  const [series, setSeries] = useState(null);

  async function getMovieTrend() {
    const { data } = await axios.get(
      "https://api.themoviedb.org/3/trending/movie/week?api_key=f3ece01977792f6f8ee4bfa412d0f7de"
    );
    setmovies(data.results.slice(0, 10));
  }
  const vote = (vote) => {
    const roundedNumber = vote.toFixed(1);
    return roundedNumber;
  }
  async function getSeriesTrend() {
    const { data } = await axios.get(
      "https://api.themoviedb.org/3/trending/tv/week?api_key=f3ece01977792f6f8ee4bfa412d0f7de"
    );
    setSeries(data.results.slice(0, 10));
  }

  const truncateTitle = (title) => {
    if (title.length > 15) {
      return title.slice(0, 15) + "...";
    }
    return title;
  };

  useEffect(() => {
    
      getMovieTrend();
      getSeriesTrend();
    }
  , []);

  return (
    <>
      {movies === null ? (
        <div className="vh-100 d-flex justify-content-center align-items-center">
          <i className="fa-solid fa-spinner fa-spin fa-7x"></i>
        </div>
      ) : (
        <div className="container mt-5">
          <div className="row ">
            <div className="col-lg-4 col-md-4 col-sm-8 col-10 mx-auto mb-sm-3 mb-3 d-flex justify-content-start align-items-center ">
              <div className="title Montserrat ">
                <hr className="w-50 mb-4 bg-info border-info " />
                <h3 className="">
                  Trending movies <br /> to watch now
                </h3>
                <p className="text-secondary text-info">
                  Most watched movies by days
                </p>
                <hr className="w-100 mt-3" />
              </div>
            </div>

            {movies
              .filter((movie) => movie.poster_path != null) // Filter out movies with null poster paths
              .map((movie) => (
                <div
                  key={movie.id}
                  className="col-lg-2 col-md-4 col-sm-6 col-10 m-auto"
                >
                  <Link
                    className="text-decoration-none text-light"
                    to={`/home/Mdetails/movie/${movie.id}`}
                  >
                    <div className="movie text-center">
                      <div className="ImgC position-relative">
                        <img
                          src={
                            `https://image.tmdb.org/t/p/w500/` +
                            movie.poster_path
                          }
                          className="w-100 rounded-1 cursor"
                          alt="movie"
                        />
                        <div className="nnnn ">
                          <i className="absoluteIcon  fa-solid fa-star text-warning ">
                            <p className=" absoluteRate fw-bolder text-white">
                              {vote(movie.vote_average)}
                            </p>
                          </i>
                        </div>
                      </div>
                      <h6 className="Montserrat p-3 mb-sm ps-0">
                        {truncateTitle(movie.title)}
                      </h6>
                    </div>
                  </Link>
                </div>
              ))}
          </div>
          {series === null ? (
            <div className="vh-100 d-flex justify-content-center align-items-center">
              <i className="fa-solid fa-spinner fa-spin fa-7x"></i>
            </div>
          ) : (
            <div className="row mt-5">
              <div className="col-lg-4 col-md-4 col-sm-8 col-10 mx-auto mb-sm-3 mb-3 d-flex justify-content-start align-items-center">
                <div className="title Montserrat ">
                  <hr className="w-50 mb-4 bg-info border-info" />
                  <h3 className="">
                    Trending Series <br /> to watch now
                  </h3>
                  <p className="text-secondary text-info">
                    Most watched Series by days
                  </p>
                  <hr className="w-100 mt-3" />
                </div>
              </div>

              {series
                .filter((ser) => ser.poster_path != null) // Filter out sers with null poster paths
                .map((ser) => (
                  <div
                    key={ser.id}
                    className="col-lg-2 col-md-4 col-sm-6 col-10 m-auto"
                  >
                    <Link
                      className="text-decoration-none text-light"
                      to={`/home/Mdetails/tv/${ser.id}`}
                    >
                      <div className="ser text-center">
                        <div className="ImgC position-relative">
                        <img
                          src={
                            `https://image.tmdb.org/t/p/w500/` +
                            ser.poster_path
                          }
                          className="w-100 rounded-1 cursor"
                          alt="ser"
                        />
                        <div className="nnnn ">
                          <i className="  absoluteIcon  fa-solid fa-star text-warning ">
                            <p className=" absoluteRate fw-bolder text-white">
                              {vote(ser.vote_average)}
                            </p>
                          </i>
                        </div>
                      </div>
                        <h6 className="Montserrat p-3 mb-sm ps-0">
                          {truncateTitle(ser.name)}
                        </h6>
                      </div>
                    </Link>
                  </div>
                ))}
            </div>
          )}
        </div>
      )}
    </>
  );
}

