/* eslint-disable jsx-a11y/anchor-has-content */
import React, { useState } from 'react'
import { Link, useNavigate } from "react-router-dom";
import { SearchContext } from "../../SearchContext.jsx";


export default function Nav() {
   const go = useNavigate();
const { setSearchTerm } = React.useContext(SearchContext);
const [inputValue, setInputValue] = useState("");

const logoutt = () => {
  localStorage.removeItem("logged");
  go("/login");
  console.log(localStorage.getItem("logged"));
}

const handleSearch = (e) => {
  e.preventDefault();
  setInputValue(e.target.value)
  setSearchTerm(inputValue);
  go("/home/search");
};

  return (
    <>
      <nav className="navbar navbar-expand-lg bg ">
        <div className="container-fluid ">
          <Link className="navbar-brand text-light" to="/home/Home">
            <h1 className="fw-bolder mt-2 ms-2">2LKA7YAN</h1>
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item ms-2 mt-2">
                <Link
                  className="fw-normal fs-5 nav-link active text-light"
                  aria-current="page"
                  to="/home/Home"
                >
                  Home
                </Link>
              </li>
              <li className="nav-item mt-2">
                <Link
                  className="fw-normal fs-5 nav-link  text-light"
                  aria-current="page"
                  to="/home/movie"
                >
                  Movies
                </Link>
              </li>
              <li className="nav-item mt-2">
                <Link
                  className="fw-normal fs-5 nav-link  text-light"
                  aria-current="page"
                  to="/home/series"
                >
                  Series
                </Link>
              </li>
            </ul>

            <form className="d-flex mt-2 mb-2">
              <input
                className="form-control me-2"
                type="search"
                placeholder="Search"
                value={inputValue}
                onChange={handleSearch}
              />
              <button id="btn" className="btn btn-outline-info" type="submit">
                Search
              </button>
            </form>

            <ul className="navbar-nav ms-auto mb-2 mb-lg-0 me-3 align-items-center">
              <li className="nav-item ms-4 mt-2">
                <a
                  href="https://www.facebook.com"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="fa-brands me-3 fa-facebook-f text-info text-decoration-none"
                ></a>
                <a
                  href="https://www.instagram.com"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="fa-brands me-3 fa-instagram text-info text-info text-decoration-none"
                ></a>
                <a
                  href="https://www.x.com"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="fa-brands me-3 fa-x-twitter text-info text-decoration-none"
                ></a>
              </li>

              <li className="nav-item ms-3 mt-2 mb-1">
                <Link onClick={logoutt}
                  className="fw-normal fs-5 nav-link text-danger"
                  to="/logOut"
                >
                  LogOut
                </Link>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
}
