// import axios from "axios";
import Joi from "joi";
import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import MNav from "../Nav/mini.jsx";

export default function Register() {

  const go = useNavigate();

  const [errors, setErrors] = useState({});
  const [success, setSuccess] = useState(false);
  const [userName , setUserName] = useState('');

  
  const [user, setUser] = useState({
    first_name: "",
    last_name: "",
    age: "",
    email: "",
    password: "",
  });

  const userData = (e) => {
    const newUser = { ...user };
    newUser[e.target.id] = e.target.value;
    setUser(newUser);

    // Validate the input field after its value is changed
    const schema = getSchema(e.target.id);
    const { error } = schema.validate(newUser[e.target.id]);

    if (error) {
      setErrors({ ...errors, [e.target.id]: error.details[0].message });
    } else {
      // Remove the error message if the input field is valid
      setErrors({ ...errors, [e.target.id]: "" });
    }
    setSuccess(false);
  };

  // Get the validation schema for a specific input field
  function getSchema(field) {
    switch (field) {
      case "first_name":
      case "last_name":
        return Joi.string().alphanum().min(3).max(20).required();
      case "age":
        return Joi.number().min(10).max(100).required();
      case "email":
        return Joi.string()
          .email({ minDomainSegments: 2, tlds: { allow: ["com", "net"] } })
          .required();
      case "password":
        return Joi.string().pattern(/^[a-zA-Z0-9]{6,30}$/i).message('Password must be at least 6 characters or numbers and less than 30').required(); ;
      default:
        return Joi.any();
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();

    // Retrieve the existing user registrations from local storage
    const existingRegistrations =
      JSON.parse(localStorage.getItem("userRegistrations")) || [];

    // Check if the provided email already exists in the registrations
    const emailExists = existingRegistrations.some(
      (existingUser) => existingUser.email === user.email
    );

    if (emailExists) {
      // Display an error message if the email already exists
      setErrors({ ...errors, email: "Email already used before." });
    } else {
      // Validate the entire user object
      const schema = Joi.object({
        first_name: getSchema("first_name"),
        last_name: getSchema("last_name"),
        age: getSchema("age"),
        email: getSchema("email"),
        password: getSchema("password"),
      });

      const { error } = schema.validate(user, { abortEarly: false });


      if (!error) {
        // Registration is successful, add user data to the registrations array
        setSuccess(true);

        const newUserRegistrations = [...existingRegistrations, user];
        // Store the updated registrations array in local storage
        localStorage.setItem(
          "userRegistrations",
          JSON.stringify(newUserRegistrations)
        );
        
        // Clear the form after successful registration
        setUserName(user.first_name)
      
        setUser({
          first_name: "",
          last_name: "",
          age: "",
          email: "",
          password: "",
        });
        setErrors({});

        go('/login')
      } else {
        // Display validation errors
        const errorMessages = {};
        error.details.forEach((detail) => {
          errorMessages[detail.context.key] = detail.message;
        });
        setErrors(errorMessages);
      }
    }
  };

  return (
    <>
    <MNav/>
      <div className="w-75 m-auto hiden py-5">
        <div className="row  ">
          <div className="col-md-9 m-auto">
            <h2 className="Montserrat fw-normal my-3 text-info fw-semibold"> Registration Form</h2>
            <form onSubmit={handleSubmit}>
              <label className="Montserrat my-2" htmlFor="first_name">
                First Name
              </label>
              <input
                onChange={userData}
                value={user.first_name}
                className="form-control Montserrat em"
                type="text"
                id="first_name"
              />
              <p className="text-danger my-2">{errors.first_name}</p>

              <label className="Montserrat my-2" htmlFor="last_name">
                Last Name
              </label>
              <input
                onChange={userData}
                value={user.last_name}
                className="form-control Montserrat em"
                type="text"
                id="last_name"
              />
              <p className="text-danger my-2">{errors.last_name}</p>

              <label className="Montserrat my-2" htmlFor="age">
                Age
              </label>
              <input
                onChange={userData}
                value={user.age}
                className="form-control Montserrat em"
                type="number"
                id="age"
              />
              <p className="text-danger my-2">{errors.age}</p>

              <label className="Montserrat my-2" htmlFor="email">
                Email
              </label>
              <input
                onChange={userData}
                value={user.email}
                className="form-control Montserrat"
                type="email"
                id="email"
              />
              <p className="text-danger my-2">{errors.email}</p>

              <label className="Montserrat my-2" htmlFor="password">
                Password
              </label>
              <input
                onChange={userData}
                value={user.password}
                className="form-control Montserrat em"
                type="password"
                id="password"
              />
              <p className="text-danger my-2">{errors.password}</p>

              <div className=" my-3 text-success text-center"><h3>{success? "welcome "+userName : ""}</h3></div>

              <div className="lo mt-4 d-flex justify-content-between align-items-center flex-wrap">
                <h5 className="Montserrat my-2">
                  Have an Account?{" "}
                  <Link
                    to="/login"
                    className="fw-semibold fs-5 text-decoration-none text-info"
                  >
                    Login Now
                  </Link>
                </h5>

                <button
                  type="submit"
                  className="btn btn-info my-2 ms-1 Montserrat me-3"
                >
                  Register
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </>
  );
}
